export const checkIdCard = function (val, isToast = false) {
    if (checkCode(val)) {
        var date = val.substring(6, 14);
        if (checkDate(date)) {
            if (checkProv(val.substring(0, 2))) {
                return true;
            }
        }
    }
    if (isToast) Toast('')
    return false;
}

export const checkqq = (val, isToast = false) => {
    //QQ511
    const qqPattern = /^[1-9][0-9]{4,10}$/
    if (!qqPattern.test(val)) {
        if (isToast) Toast('qq')
    }
    return qqPattern.test(val)
}

export const checkEmail = (val, isToast = false) => {
    const reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/;
    if (!reg.test(val)) {
        if (isToast) Toast('')
    }
    return reg.test(val)
}

export const checkPhone = (val, isToast = false) => {
    const reg = /^1[3456789]\d{9}$/;
    console.log(val);
    if (!reg.test(val)) {
        if (isToast) Toast('')
    }
    return reg.test(val)
}