<template>
    <div class="person-info">
        <div class="tip"></div>
        <div class="block">
            <cell title="" defaultTxt="" v-model="form.userName" type="input" placeholder=""></cell>
            <cell title="" defaultTxt="" v-model="form.idCard" type="input" placeholder=""></cell>
            <cell title="">
                <div class="block-picker" @click="showPicker('', 'educations', 'educationLevel')">{{ArrayToObj(educations, 'value', 'text')[form.educationLevel] || ''}} ></div>
            </cell>
            <cell title="">
                <div class="block-picker" @click="showPicker('', 'marrys', 'marriage')">{{ArrayToObj(marrys, 'value', 'text')[form.marriage] || ''}} ></div>
            </cell>
            <cell title="">
                <div class="block-picker" @click="showPicker('', 'childCounts', 'childCount')">{{ArrayToObj(childCounts, 'value', 'text')[form.childCount] || ''}} ></div>
            </cell>
            <cell title="QQ" defaultTxt="" v-model="form.qqNum" type="input" placeholder="QQ"></cell>
            <cell title="" defaultTxt="" v-model="form.email" type="input" placeholder=""></cell>
        </div>
        <div class="block">
            <cell title="">
                <div class="block-picker" @click="showAddressPicker('', 'adress')">{{form.adress || ''}} ></div>
            </cell>
            <div class="details dash-line">
                <input type="text" class="bottom" v-model="form.adressDetails" placeholder="">
            </div>
            <cell title="">
                <div class="block-picker" @click="showAddressPicker('', 'companyAdress')">{{form.companyAdress || ''}} ></div>
            </cell>
            <div class="details dash-line">
                <input type="text" v-model="form.companyAdressDetails" placeholder="">
            </div>
        </div>
        <div class="block">
            <cell title="" defaultTxt="" v-model="form.companyName" type="input" placeholder=""></cell>
            <cell title="">
                <div class="block-picker" @click="showPicker('', 'jobKinds', 'job')">{{ArrayToObj(jobKinds, 'value', 'text')[form.job] || ''}} ></div>
            </cell>
            <cell title="">
                <div class="block-picker" @click="showPicker('', 'jobTimes', 'workingTime')">{{ArrayToObj(jobTimes, 'value', 'text')[form.workingTime] || ''}} ></div>
            </cell>
        </div>
        <div class="block btn-block">
            <cube-button class="btn-main re-btn" @click="nextStep" :primary="true"></cube-button>
        </div>
    </div>
</template>

<script>
import cell from '../components/cell'
import { ArrayToObj } from '../utils/utils.js'
import { provinceList, cityList, areaList } from '../utils/area.js'
import { checkIdCard, checkqq, checkEmail } from '../utils/validator.js'
import { getMyInfo } from '../api/axios.js'
const addressData = provinceList
addressData.forEach(province => {
    province.children = cityList[province.value]
    province.children.forEach(city => {
        city.children = areaList[city.value]
    })
})

export default {
    data() {
        return {
            form: {
                userName: '',
                idCard: '',
                educationLevel: '',
                marriage: '',
                childCount: '',
                qqNum: '',
                email: '',
                companyName: '',
                // addressNow: '',
                adress: '',
                adressDetails: '',
                // companyAddress: '',
                companyAdress: '',
                companyAdressDetails: '',
                job: '',
                workingTime: ''
            },
            /// 
            educations: [
                { value: 101, text: '' },
                { value: 102, text: '' },
                { value: 103, text: '' },
                { value: 104, text: '/' },
                { value: 105, text: '' }
            ],
            /// 
            marrys: [
                { value: 101, text: '' },
                { value: 102, text: '' },
                { value: 103, text: '' },
                { value: 104, text: '' }
            ],
            /// 
            childCounts: [
                { value: 0, text: '' },
                { value: 1, text: '1' },
                { value: 2, text: '2' },
                { value: 3, text: '3' },
                { value: 4, text: '4' }
            ],
            /// 
            jobKinds: [
                { value: 101, text: '/' },
                { value: 102, text: '' },
                { value: 103, text: '//' },
                { value: 104, text: '//' },
                { value: 105, text: '' },
                { value: 106, text: '' },
                { value: 107, text: '' }
            ],
            /// 
            jobTimes: [
                { value: 101, text: '3' },
                { value: 102, text: '3-6' },
                { value: 103, text: '6-1' },
                { value: 104, text: '1' },
                { value: 105, text: '3' },
                { value: 106, text: '' }
            ]
        }
    },
    components: {
        cell
    },
    beforeCreate() {
        document.title = ''
    },
    methods: {
        ArrayToObj,
        showPicker(title, list, label) {
            this.picker = this.$createPicker({
                title,
                data: [this[list]],
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                    console.log(selectedVal)
                    this.form[label] = selectedVal[0]
                },
                onCancel: () => {}
            }).show()
        },
        showAddressPicker(title, label) {
            this.addressPicker = this.$createCascadePicker({
                title,
                data: addressData,
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                    this.form[label] = selectedText.join(',')
                },
                onCancel: () => {}
            }).show()
        },
        async nextStep() {
            if (Object.values(this.form).some(x => x === '')) {
                this.$store.commit('setMsg', '')
                return
            }
            if (!checkIdCard(this.form.idCard, true)) return
            if (!checkqq(this.form.qqNum, true)) return
            if (!checkEmail(this.form.email, true)) return
            let form = {
                ...this.form,
                addressNow: this.form.adress + this.form.adressDetails,
                companyAddress: this.form.companyAdress + this.form.companyAdressDetails
            }
            this.$router.push('/personContact')
            // this.$router.push({
            //     name: 'personContact',
            //     params: { form }
            // })
        }
    }
}
</script>

